<template>
  <div class="main">
    <auto-header>
      <template v-slot:left>
        <div class="search hidden-sm-and-down">
          <img class="icon" src="../assets/img/search.png" alt="" />
          <el-input v-model="searchContent" placeholder="Search" />
        </div>
        <rect-button
          class="small-search hidden-md-and-up"
          width="48px"
          @click="showSearchBar = !showSearchBar"
        >
          <i class="el-icon-search"></i>
        </rect-button>
      </template>
      <h1>Workspace</h1>
      <template v-slot:right>
        <div class="create hidden-sm-and-down">
          <rect-button @click="checkEnvrionment" width="122px">
            Create
          </rect-button>
        </div>
        <rect-button
          class="small-search hidden-md-and-up"
          @click="checkEnvrionment"
          width="48px"
        >
          <i class="el-icon-plus"></i>
        </rect-button>
      </template>
    </auto-header>
    <div class="home" v-loading="fullscreenLoading">
      <div class="search-bar">
        <div class="search hidden-md-and-up" v-show="showSearchBar">
          <img class="icon" src="../assets/img/search.png" alt="" />
          <el-input v-model="searchContent" placeholder="Search" />
        </div>
      </div>
      <el-row :gutter="20">
        <el-col
          :xs="24"
          :sm="8"
          :md="5.9"
          :lg="6"
          :xl="6"
          v-for="(workspace, index) in availableWorkspace"
          :key="index"
        >
          <div class="list-item" @click="toProposals(workspace)">
            <div class="image hidden-xs-only">
              <span v-html="getAvatar(workspace)"></span>
            </div>
            <div class="small-image hidden-sm-and-up">
              <span v-html="getAvatar(workspace)"></span>
            </div>
            <div class="bold">{{ workspace.name }}</div>
            <div class="text">{{ workspace.spec }}</div>
          </div>
        </el-col>
      </el-row>
      <metamask-dialog
        :showDialog="showMetamaskDialog"
        @onClose="showMetamaskDialog = false"
      ></metamask-dialog>
      <custom-rpc-dialog
        :showDialog="showRpcDialog"
        @onClose="showRpcDialog = false"
      ></custom-rpc-dialog>
      <el-dialog
        title=""
        center
        style="font-weight:bold"
        :visible.sync="showWorkspaceDialog"
        custom-class="dialog-style"
        :close-on-click-modal="false"
        :fullscreen="isSmallScreen"
        width="600px"
      >
        <div class="detail">
          <img class="image" src="../assets/img/favicon.png" alt="automata" />
          <el-select
            class="selecter"
            v-model="newWorkSpace.chainId"
            placeholder="Select"
          >
            <el-option
              v-for="item in networks"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <div class="new_workspace">
            <el-input
              placeHolder="Please enter your workspace name"
              v-model="newWorkSpace.name"
            ></el-input>
          </div>
          <div class="new_workspace">
            <el-input
              placeHolder="Please enter your workspace spec"
              v-model="newWorkSpace.spec"
            ></el-input>
          </div>
          <div class="new_workspace">
            <el-input
              placeHolder="Contract address (0x1d3...3f1)"
              v-model="newWorkSpace.contract"
            ></el-input>
          </div>
          <div class="reminder">
            Only Kovan Test Network is supported in test phase.
          </div>
          <div class="btn">
            <rect-button width="200px" @click="createWorkSpace"
              >Create</rect-button
            >
          </div>
          <!-- TODO Remove before launch -->
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  web3,
  addWorkSpace,
  subscribeEvent,
  checkMetaMask
} from "../matalab/witness.js";
import Network from "../matalab/global";
import Avatars from "@dicebear/avatars";
import sprites from "@dicebear/avatars-identicon-sprites";
import AutoHeader from "../components/AutoHeader.vue";
import RectButton from "../components/RectButton.vue";
import MetamaskDialog from "../components/MetamaskDialog.vue";
import CustomRpcDialog from "../components/CustomRpcDialog.vue";
import { mapGetters } from "vuex";

export default {
  name: "home",
  data() {
    return {
      searchContent: "",
      searchResult: [],
      showWorkspaceDialog: false,
      newWorkSpace: {
        name: "",
        spec: "",
        contract: "",
        chainId: Network.chainInfo.id
      },
      showMetamaskDialog: false,
      showRpcDialog: false,
      showSearchBar: false,
      networks: [{ value: Network.chainInfo.id, label: Network.chainInfo.name }]
    };
  },
  components: {
    AutoHeader,
    RectButton,
    MetamaskDialog,
    CustomRpcDialog
  },
  async mounted() {
    this.fullscreenLoading = true;
    this.$store.dispatch("getWorkspace");
    this.fullscreenLoading = false;
  },
  watch: {
    searchContent() {
      if (this.searchContent && this.searchContent !== "") {
        let result = [];
        for (let workspace of this.allWorkSpace) {
          let search = this.searchContent.toLowerCase();
          if (
            workspace &&
            workspace.name &&
            workspace.name.toLowerCase().includes(search)
          ) {
            result.push(workspace);
          }
        }
        this.searchResult = result;
      }
    }
  },
  computed: {
    availableWorkspace() {
      if (this.searchContent === "") {
        return this.allWorkSpace;
      } else {
        return this.searchResult;
      }
    },

    spacesInfo() {
      let spaceNum = this.allWorkSpace.length || 0;
      if (spaceNum === 0) return "";
      else return `(${spaceNum})`;
    },

    fullscreenLoading: {
      get: function() {
        return this.loadWorkspace && this.allWorkSpace.length === 0;
      },
      set: () => {}
    },

    isSmallScreen() {
      return window.innerWidth < 650;
    },

    ...mapGetters({
      allWorkSpace: "workspaces",
      loadWorkspace: "loadWorkspace"
    })
  },
  methods: {
    toProposals(workspace) {
      this.$router.push({
        name: "workspace",
        params: {
          id: workspace.id,
          name: workspace.name,
          contract: workspace.contract
        }
      });
    },

    validateNotify(message) {
      this.$notify.error({ title: "Error", message: message, offset: 100 });
    },

    async validateNewWorkspace() {
      const check = await this.checkNetwork();
      if (check) {
        if (this.newWorkSpace.name === "") {
          this.validateNotify("Workspace name can not be empty");
          return false;
        } else if (this.newWorkSpace.spec === "") {
          this.validateNotify("Workspace spec can not be empty");
          return false;
        } else if (this.newWorkSpace.contract !== "") {
          let isAddress = web3.utils.isAddress(this.newWorkSpace.contract);
          if (isAddress) {
            return true;
          } else {
            this.validateNotify("Invalid contract address");
            return false;
          }
        } else {
          this.validateNotify("Contract address can not be empty");
          return false;
        }
      } else {
        return false;
      }
    },

    async createWorkSpace() {
      const valid = await this.validateNewWorkspace();
      if (valid) {
        this.fullscreenLoading = true;
        let addSpace = addWorkSpace(this.newWorkSpace);
        addSpace
          .then(res => {
            console.debug(res);
            this.fullscreenLoading = false;
            this.$notify({
              title: "Congratulations",
              message: "Workspce created successfully",
              offset: 100,
              type: "success"
            });
          })
          .catch(err => {
            console.debug(err);
            this.fullscreenLoading = false;
            this.$notify({
              title: "Error",
              message: "Workspce created failed",
              offset: 100,
              type: "error"
            });
          });
        await subscribeEvent("CreateWorkspace", async function(event) {
          console.log(event);
          this.$store.dispatch("getWorkspace");
        });
        this.newWorkSpace = {
          name: "",
          spec: "",
          contract: ""
        };
        this.showWorkspaceDialog = false;
      }
    },

    getAvatar(workspace) {
      let options = {
        radius: 10
      };
      let avatars = new Avatars(sprites, options);
      let svg = avatars.create(
        workspace.name + workspace.id + "automata.network"
      );
      // let s = blobs2.svg({
      //   seed: workspace.name+workspace.id+'automata.network',
      //   extraPoints: 6,
      //   randomness: 3,
      //   size: 80,},
      //   {
      //   fill: "#FBBF24",
      //   stroke: "black",
      //   strokeWidth: 3,
      //   },
      // );
      return svg;
    },

    async checkEnvrionment() {
      this.showRpcDialog = false;
      this.showMetamaskDialog = false;
      const installed = await checkMetaMask();
      if (installed) {
        let chainId = await window.ethereum.request({ method: "eth_chainId" });
        if (parseInt(chainId) === Network.automataChainId) {
          this.showWorkspaceDialog = true;
        } else {
          this.showRpcDialog = true;
        }
      } else {
        this.showMetamaskDialog = true;
      }
    },

    async checkNetwork() {
      const installed = await checkMetaMask();
      if (installed) {
        let chainId = await window.ethereum.request({ method: "eth_chainId" });
        if (parseInt(chainId) === Network.automataChainId) {
          return true;
        } else {
          this.validateNotify(
            "You need to connect to Automata network to continue."
          );
          return false;
        }
      } else {
        this.validateNotify(
          "Looks like MetaMask extension is missing on your browser."
        );
        return false;
      }
    }
  }
};
</script>

